.h2 {
  text-decoration: none;
  color: rgb(104, 114, 255);
}
.contain {
  background: rgba(255, 255, 255, 0.25);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  margin-top: 1rem;
  margin-left: 3rem;
  margin-right: 3rem;
  padding: 1rem;
}

.contain:hover {
  background: rgba(255, 101, 101, 0.25);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border-radius: 10px;
}

.fuerza{
  color:brown;
  font-size: 1.2rem;
  font-weight: bold;;
}
.tipo{
  color:rgb(44, 54, 42);
  font-size: 1.2rem;
  font-weight: bold;;

}
